import React, { useState, Component } from 'react';
import OnSitePost from './elements/onsitePost'




class SubmitPostField extends Component {

    render() {
        const { submitError, submitErrorMessage,updateActive, wall, onClickToaddMore, onSubmitPostData, upload_image, onClickToRemove, isEdit, onTextName, onTextEmail, email, name, nameError, emailError, onClickToEdit, apiRequest, snapContent } = this.props;
        const { infoH, infoSubH, inputNL, inputNPH, inputNErr, inputEL, inputEPH, inputEErr, termsText, termsLT, onePostErr, detailErr, addMPBtn, submitBtn } = snapContent;
        return <>

            <div className='box_title'>
                <h2 className='title'>{infoH}</h2>
                <p className='sub_heading'><sup>*</sup> {infoSubH}</p>
            </div>
            <div className='box_body'>
                <div className='submit-post-main'>
                    {
                        (upload_image && upload_image.length > 0) ? <OnSitePost key={1} upload_image={upload_image} onClickToRemove={onClickToRemove} isEdit={isEdit} onClickToEdit={onClickToEdit} /> : null
                    }
                    <div className='following_information'>
                        <div>
                            <div className='form-group'>
                                <label>{inputNL}<span>*</span></label>
                                <input type="text" className='input-fileld' placeholder={inputNPH} onChange={onTextName} value={name} />
                                {nameError ? <div className='error-text'>* {inputNErr}</div> : null}
                            </div>
                            <div className='form-group'>
                                <label>{inputEL}<span>*</span></label>
                                <input type="text" className='input-fileld' placeholder={inputEPH} onChange={onTextEmail} value={email} />
                                {emailError ? <div className='error-text'>* {inputEErr}</div> : null}
                            </div>
                            {/* <div className='form-group'>
                                <label className='checkbox'><input type="checkbox" checked={receiveMail} onClick={onUpdatereceiveMail} /><span></span> I want to receive email updates.</label>
                            </div> */}
                            <p className='terms_condition'>{termsText} <a href={wall.UgcSettings.onsite_terms_url} target="_blank">{termsLT} *</a></p>
                            {submitError ? <div className='error-text submit-error'>* {submitErrorMessage ? submitErrorMessage : upload_image && upload_image.length == 0 ? onePostErr : detailErr}</div> : null}
                            <div className='form_footer'>
                                <div className='add_more'>
                                    <button className='btn-addmore' onClick={()=>{onClickToaddMore();updateActive()}}>
                                        + {addMPBtn}
                                    </button>
                                    <span><span>*</span> Max 20 Posts</span>
                                </div>
                                <div className='submit_btn'>

                                    <button className={`btn-primary ${apiRequest ? `submit_pic_spinner` : ``}`} onClick={onSubmitPostData}>{submitBtn}</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </>

    }
}

export default SubmitPostField;