export const iniFrame = () => {
    if (window.self !== window.top) return true
    else return false
}

export const UGC_SUITE_WALL = () => {

    try {
        if (window.wallID) return false
        else {
            const windowHref = window.location.href;
            if (windowHref.includes('display.taggbox.com')) {
                let wallid = window.location.href.split("https://display.taggbox.com/")[1];
                if (wallid && String(wallid).length > 0 && String(wallid)[0] == "0") {
                    return true
                }
                else return false
            }
            else if (windowHref.includes('onsite-upload')) {
                let wPath = window.location.pathname.split('/s/')
                let sSplit = wPath[1].split('/');
                let wallId = sSplit.length > 0 ? sSplit[0] : ``
                if (wallId && String(wallId).length > 0 && String(wallId)[0] == "0") {
                    return true
                }
                else return false
            }
            else {
                let checkIndex = window.location.pathname.split('/index')
                let wallID = null;
                if (checkIndex.length > 1) {
                    wallID = checkIndex[1].split('/')[1];
                    if (wallID.includes('/editor')) {
                        wallID = wallID.replace("/editor", "");
                    }
                    if (wallID && String(wallID).length > 0 && String(wallID)[0] == "0") {
                        return true
                    }
                    else return false

                }
                else {
                    let wPath = window.location.pathname.split('/s/')
                    let sSplit = wPath[1].split('/');
                    if (sSplit.length > 1) {
                        if (sSplit[1] === "editor") wallID = sSplit[0];
                        else wallID = sSplit[1];
                    }
                    else if (sSplit.length === 1) wallID = sSplit[0];
                    if (wallID.includes('/editor')) {
                        wallID = wallID.replace("/editor", "");
                    }
                    if (wallID && String(wallID).length > 0 && String(wallID)[0] == "0") {
                        return true
                    }
                    else return false
                }
            }
        }

    }
    catch (ex) {
        return false
    }
}


export const ACTIVE_SCREEN_KEY = UGC_SUITE_WALL() ? `ugc_active-screen` : `active-screen`
export const REMOVE_SCREEN_KEY = UGC_SUITE_WALL() ? `ugc_remove-screen` : `remove-screen`
export const DELETE_FEED = UGC_SUITE_WALL() ? `ugc_deleteFeed_` : `deleteFeed_`
export const POST_OBJECT = UGC_SUITE_WALL() ? `ugc_post_object_` : `post_object_`
export const POST_STATUS = UGC_SUITE_WALL() ? `ugc_poststatus` : `poststatus`
export const BROAD_CAST_WALL = UGC_SUITE_WALL() ? `ugc_broadcastwall` : `broadcastwall`

export const UGC_SUITE_KEY = UGC_SUITE_WALL() ? `&ugcSuite=1` : ``

