import React from 'react';
import { CLOUD_PATH, SERVER_PATH } from '../../constants';

const BannerLogo = ({ LogoImage, image_path }) => {
    let logoImage = `${SERVER_PATH}${LogoImage.path}${LogoImage.newName}`;
    if(LogoImage.newName.includes('https://cloud.tagbox.com/')){
        logoImage = LogoImage.newName;
    }
    return <img src={(LogoImage.newName  === "logo_bnr-5.svg" || LogoImage.newName  === "logo_bnr-3.svg" || LogoImage.newName  === "logo_bnr-2.svg") ? 
     `${CLOUD_PATH}react-app/media/banner/${LogoImage.newName}` : LogoImage.newName.includes('logo_bnr') ? LogoImage.newName : LogoImage.newName != '' ? logoImage : ''} className="img-responsive" style={{ maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain" }} />
}

export default BannerLogo;