import React, { Component } from 'react';
class AddCaption extends Component {
    render() {
        const { onClickToUpdateStep, isActiveIndex, isAddMoreActive, addCaption, item, onClickToUpdateMoreStep, snapContent } = this.props;
        const { captionPH, captionErr, skipBtn, nextBtn } = snapContent;
        return (
            <div className='add_caption_tab'>
                <div className='add-text'>
                    <input type="text" className="input-fileld" placeholder={captionPH} onChange={addCaption(isActiveIndex)} value={(item && item.caption) ? item.caption : ""} />
                    {item.error ? <div className='error-text'>* {captionErr}</div> : null}
                </div>
                <div className='next-step' style={item && isAddMoreActive ? { display: 'flex', justifyContent: 'space-between' } : null}>
                    {item && isAddMoreActive ? <button type='submit' className='btn-primary' onClick={onClickToUpdateMoreStep(2)}>{skipBtn}</button> : null}
                    <button type='submit' className='btn-primary' id="tb_onsite_btn_id" onClick={onClickToUpdateStep(2)}>{nextBtn}</button>
                </div>
            </div>
        )
    }
}

export default AddCaption;