
import { IS_LIVE } from '../constants'
import CloseButton from './elements/closeButton'
import OnsiteLogo from './elements/onSitelogo'
import AddUpload from './elements/upload'
import OnSitePost from './elements/onsitePost'
import Success from './success';
import { submitPostData } from '../actions/action'
import io from "socket.io-client";
import AddCaption from './addCaption';
import SubmitPostField from './submitPost';
import SubmitReview from './submitReview';
import { UGC_SUITE_WALL, POST_STATUS, UGC_SUITE_KEY } from '../../utils'

import React, { memo, Component } from "react";
import { Helmet } from "react-helmet";
import './OnsitePopup.css'
import { HOST_URL } from '../../studioPreview/actions/api'
import getsnapUpLabels from './elements/content';

const OnsiteStripStyle = ({ onsite_txt_font_variation, onsite_txt_font, onsite_txt_css_font }) => {
    return <Helmet><link href={`https://fonts.googleapis.com/css?family=${onsite_txt_css_font}:${onsite_txt_font_variation}`} rel="stylesheet" /></Helmet>
}

const validateEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

const postInitialData = {
    file: null,
    caption: null,
    review: null,
    rating: 0,
    error: false,
    activeTab: 1,
    type: 1,
    thumb_file: "",
    videoPath: ""
}

class OnSiteUpload extends Component {


    state = {
        submitPost: true,
        activeTab: 1,
        isOpen: true,
        step: 1,
        wall_id: null,
        name: null,
        email: null,
        upload_image: [{
            file: null,
            caption: null,
            review: null,
            rating: 0,
            error: false,
            activeTab: 1,
            type: 1,
            thumb_file: "",
            videoPath: ""
        }],
        receiveMail: false,
        tandc: true,
        nameError: false,
        emailError: false,
        submitErrorMessage: null,
        apiRequest: false,
        isActiveIndex: 0,
        isAddMoreActive: false,
        isEdit: false,
        submitError: false
    }
    handleNext = event => this.setState({ submitPost: false })
    hanleTab1 = event => this.setState({ activeTab: 1 })
    hanleTab2 = event => this.setState({ activeTab: 2 })


    componentDidMount() {
        const { UgcSettings } = this.props;
        if (UgcSettings.choose_content_category == 3) {
            this.setState({ activeTab: 2 })
        }
    }

    updateActive = () => {
        const { UgcSettings } = this.props;
        if (UgcSettings.choose_content_category == 3) {
            this.setState({ activeTab: 2 })
        }
    }

    onShowCloseSubmitPicPopUp = event => {

        const { isOpen } = this.state;
        this.setState({ isOpen: !isOpen })

    }
    updateImageWithData = (data) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length < 20) {
            upload_image.push(data);
            this.setState({ upload_image })
        }
    }


    onClickToaddMore = event => {
        const { upload_image, isActiveIndex } = this.state;
        //console.log("onClickToaddMore upload_image", upload_image.length)
        if (upload_image && upload_image.length < 20) {
            upload_image.push({ file: null, caption: null, review: null, rating: 0, error: false, activeTab: 1, type: 1, thumb_file: "", videoPath: "" })
            // console.log("onClickToaddMore", upload_image.length, isActiveIndex, upload_image.length == 0 || upload_image.length == 1 ? 0 : parseInt(upload_image.length) - 1)
            this.setState({ isAddMoreActive: true, upload_image, isActiveIndex: upload_image.length == 0 || upload_image.length == 1 ? 0 : parseInt(upload_image.length) - 1, step: 1, activeTab: 1, submitError: false })
        }

    }
    onClickToUpdateMoreStep = (step) => event => {
        const { isAddMoreActive, isActiveIndex, upload_image } = this.state;
        if (isAddMoreActive && upload_image[isActiveIndex]) {
            upload_image.splice(isActiveIndex, 1);
        }
        this.setState({ isAddMoreActive: false, upload_image, step, isEdit: false, isActiveIndex: upload_image && upload_image.length > 0 && (parseInt(upload_image.length) - 1) > 0 ? (parseInt(upload_image.length) - 1) : 0, activeTab: 1 })
    }
    onClickToUpdateStep = (step) => event => {
        var isError = false;
        const { upload_image, isActiveIndex, activeTab } = this.state;
        if (upload_image && upload_image[isActiveIndex]) {
            if (activeTab == 1) {
                if (upload_image[isActiveIndex].caption == null || upload_image[isActiveIndex].caption == "" || upload_image[isActiveIndex].caption.length == 0) {
                    upload_image[isActiveIndex].error = true;
                    isError = true;
                }
                else upload_image[isActiveIndex].error = false

            }
            else if (activeTab == 2) {
                if (upload_image[isActiveIndex].review == null || upload_image[isActiveIndex].review == "" || upload_image[isActiveIndex].review.length == 0 || upload_image[isActiveIndex].rating == 0) {
                    upload_image[isActiveIndex].error = true;
                    isError = true;
                }
                else upload_image[isActiveIndex].error = false
            }

        }
        if (!isError) this.setState({ isAddMoreActive: false, upload_image, step, isEdit: false, activeTab: 1, submitErrorMessage: null })
        else this.setState({ upload_image })



    }


    onSubmitPostData = event => {

        const { name, email, upload_image, tandc, receiveMail } = this.state;
        const { wall, feed } = this.props;

        var ugcSuite = wall.ugcSuite == 1 ? true : false
        var feedData = {}
        if (ugcSuite) {
            if (feed && Object.keys(feed).length > 0) {
                if (feed.Network && Object.keys(feed.Network).length > 0 && feed.Network.id == 29 && feed.value1) {
                    feedData = { feedId: feed.value1 }
                }
            }
        }

        this.setState({ submitError: false })

        if (tandc && (name && name.length > 0) && (email && email.length > 0 && validateEmail(email) && !email.includes("mailinator")) && (upload_image && upload_image.length > 0) && tandc) {
            this.setState({ apiRequest: true })
            submitPostData({ wall_id: this.props.wall.id, name, email, upload_file: upload_image, receive_mail: receiveMail, onsite_token: this.props.onsite_token, ...feedData }, { headers: ugcSuite ? { Ugcsuite: 1 } : {} }).then((response) => {
                const { message } = response.data;
                // this.props.onSiteTokenUpdate(onsite_token)
              
                this.props.onSiteUpdateToken(this.props.onsite_token)

                if (message && message.length) {
                    const query = UGC_SUITE_KEY
                    const SOCKET_CONNECTION = io(`${HOST_URL}`, {
                        transports: ["websocket"],
                        transport: ["websocket"],
                        query: query,
                        upgrade: true,
                    });

                    let id = message[0].id;
                    let synced = message[0].synced;
                    SOCKET_CONNECTION.emit(POST_STATUS, [id, synced]);
                }

                this.setState({ step: 3, apiRequest: false }, () => setTimeout(this.onClosePopUp, 2000))
            })
            .catch((error) => {  
                const { message } = error.toJSON();
                if (message) {
                    this.setState({ submitError: true, submitErrorMessage: message, apiRequest: false })
                }
            });
        }
        else {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true, apiRequest: false })
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true, apiRequest: false })
            if (upload_image && upload_image.length == 0) this.setState({ submitError: true })

        }

    }



    addCaption = (activeIndex) => event => {
        const { upload_image } = this.state;

        if (upload_image && upload_image[activeIndex]) {

            upload_image[activeIndex].caption = event.target.value;
            upload_image[activeIndex].review = "";
            upload_image[activeIndex].rating = 0;
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 1;


            this.setState({ upload_image })
        }
    }

    addReview = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].review = event.target.value;
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 2;
            this.setState({ upload_image })
        }
    }



    addRatings = (rating, activeIndex) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].caption = "";
            upload_image[activeIndex].rating = rating;
            upload_image[activeIndex].error = false;
            upload_image[activeIndex].activeTab = 2;
            this.setState({ upload_image })
        }
    }
    addPostImage = (file, thumb_file, videoPath, type, activeIndex, ext, size, fileName, mediaHeight, mediaWidth) => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].file = file;
            upload_image[activeIndex].thumb_file = thumb_file;
            upload_image[activeIndex].videoPath = videoPath;
            upload_image[activeIndex].type = type;
            upload_image[activeIndex].ext = ext;
            upload_image[activeIndex].size = size;
            upload_image[activeIndex].fileName = fileName;
            upload_image[activeIndex].mediaHeight = mediaHeight;
            upload_image[activeIndex].mediaWidth = mediaWidth;
            this.setState({ upload_image })
        }
    }

    onClickToRemove = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            upload_image.splice(activeIndex, 1);
        }
        this.setState({ upload_image })
    }


    onClickToImageRemove = (activeIndex) => event => {
        const { upload_image } = this.state;
        if (upload_image && upload_image[activeIndex]) {
            upload_image[activeIndex].file = null;
            upload_image[activeIndex].thumb_file = "";
            upload_image[activeIndex].videoPath = "";
            upload_image[activeIndex].type = 1;
            upload_image[activeIndex].ext = null;
            upload_image[activeIndex].size = 0;
            upload_image[activeIndex].fileName = null;
            upload_image[activeIndex].mediaHeight = 0;
            upload_image[activeIndex].mediaWidth = 0;
            this.setState({ upload_image })
        }
    }
    onClickToEdit = (activeIndex) => event => {
        const { upload_image } = this.state;
        let isActiveTab = 1;
        if (upload_image && upload_image[activeIndex]) {
            isActiveTab = upload_image[activeIndex].activeTab;
        }
        this.setState({ isActiveIndex: activeIndex, isEdit: true, step: 1, activeTab: isActiveTab })
    }
    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }
    onTextEmail = event => {
        this.setState({ email: event.target.value, emailError: false }, () => {
            if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
        })
    }

    onUpdatereceiveMail = event => {
        const { receiveMail } = this.state;
        this.setState({ receiveMail: !receiveMail })
    }
    onUpdatetandc = event => {
        const { tandc } = this.state;
        this.setState({ tandc: !tandc })
    }

    onClosePopUp = event => {
        if (window.location.href && window.location.href.includes("onsite-upload")) {
            const { wall } = this.props;
            const onsitePath = '/onsite-upload=true';
            if (IS_LIVE) window.location.href = `https://display.taggbox.com/${UGC_SUITE_WALL() ? `0` : ``}${wall.id}${wall.owner == 149146 ? onsitePath : ''}`;
            else window.location.href = `https://test.taggbox.com/display/s/${UGC_SUITE_WALL() ? `0` : ``}${wall.id}${onsitePath}`;
        }
    }

    render() {

        const { UgcSettings, wall } = this.props;
        const { activeTab, isActiveIndex, step, upload_image, isAddMoreActive, isEdit, name, email, receiveMail, tandc, nameError, emailError, apiRequest, submitError, submitErrorMessage } = this.state;
        const { onClosePopUp } = this;
        const isUploderActive =  wall && wall.UgcSettings && Object.keys(wall.UgcSettings).length > 0;
        const snapContent = getsnapUpLabels(isUploderActive && wall.UgcSettings.onsite_language ? wall.UgcSettings.onsite_language :'english');
        const { captionTabT, reviewTabT } = snapContent;

        return isUploderActive ?
            <div className='onsite_submit_post_main' style={{ backgroundImage: `url(${UgcSettings.onsite_background_image})`, backgroundColor: UgcSettings.onsite_background_color }}>
                <div className='onsite_submit_post_inner'>
                    <div className='submit_post_box'>
                        <div className='box_header'>
                            <CloseButton onClosePopUp={onClosePopUp} />
                            <OnsiteLogo wall={wall} />
                        </div>
                        {step == 1 ? <>
                            <div className='box_title'>
                                <h2 className='title'>{UgcSettings.onsite_status ? UgcSettings.onsite_title : `Share Your Images With Us`}</h2>
                                {/* <p className='sub_heading'>{ wall.UgcSettings.onsite_desc }</p> */}
                                <p className='sub_heading'>{UgcSettings.onsite_status ? wall.UgcSettings.onsite_popup_sub_heading : `Upload your image featuring our brand products and get a chance to feature in our UGC lookbook. `}</p>
                            </div>
                            <div className='box_body'>
                                <div className='submit_post_tabs'>
                                    {UgcSettings.choose_content_category == 1 ? <> <span className='saprator'>OR</span> 
                                        <ul className='tabs_nav'>
                                            <li className={`${activeTab == 1 ? "active" : ""}`} onClick={this.hanleTab1}>{captionTabT}</li>
                                            <li  className={`${activeTab == 2  ? "active" : ""}`} onClick={this.hanleTab2}>{reviewTabT}</li>
                                        </ul> </>
                                    : null}

                                </div>

                                <div className='post_tabs_content'>
                                    {/* {
                                        isEdit && (upload_image && upload_image.length > 0) ? <OnSitePost upload_image={upload_image} isEdit={isEdit} isActiveIndex={isActiveIndex} onClickToRemove={this.onClickToRemove} /> : null
                                    } */}


                                    <AddUpload key={isActiveIndex} updateImageWithData={this.updateImageWithData} isActiveIndex={isActiveIndex} addPostImage={this.addPostImage} item={upload_image[isActiveIndex]} onClickToImageRemove={this.onClickToImageRemove} snapContent={snapContent}/>

                                    {activeTab == 1 && UgcSettings.choose_content_category !== '3' ? <AddCaption hasActive={this.handleNext} isActiveIndex={isActiveIndex} onClickToUpdateStep={this.onClickToUpdateStep} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} isAddMoreActive={isAddMoreActive} addCaption={this.addCaption} item={upload_image[isActiveIndex]} snapContent={snapContent}/> : <SubmitReview isActiveIndex={isActiveIndex} onClickToUpdateStep={this.onClickToUpdateStep} onClickToUpdateMoreStep={this.onClickToUpdateMoreStep} isAddMoreActive={isAddMoreActive} addReview={this.addReview} item={upload_image[isActiveIndex]} addRatings={this.addRatings} snapContent={snapContent}/>}
                                </div>
                            </div>
                        </> : step == 2 ? <SubmitPostField updateActive={this.updateActive} submitError={submitError} submitErrorMessage={submitErrorMessage} onClickToaddMore={this.onClickToaddMore} onSubmitPostData={this.onSubmitPostData} upload_image={upload_image} isEdit={isEdit} onClickToRemove={this.onClickToRemove} onTextName={this.onTextName} onTextEmail={this.onTextEmail} name={name} email={email} receiveMail={receiveMail} tandc={tandc} onUpdatereceiveMail={this.onUpdatereceiveMail} nameError={nameError} emailError={emailError} wall={wall} onClickToEdit={this.onClickToEdit} apiRequest={apiRequest} snapContent={snapContent} /> : step == 3 ? <Success wall={wall} onClosePopUp={onClosePopUp} /> : null}
                    </div>
                </div>
            </div> : null









        //   {
        //     UgcSettings && Object.keys(UgcSettings).length > 0 ? <OnsiteStripStyle {...UgcSettings} /> : null
        //   }
        //   <div className={`display-onsiteupload ${UgcSettings.onsite_overlay_poisition == "1" ? `onsiteupload_top_position` : UgcSettings.onsite_overlay_poisition == "2" ? `onsiteupload_bottom_position` : ``}`} style={{ backgroundColor: UgcSettings.onsite_bg_color, color: UgcSettings.onsite_txt_color, height: `${UgcSettings.onsite_overlay_height}px`, bottom: (CustomBanner.status === 1 && CustomBanner.banner_position === "2") ? CustomBanner.banner_height : 0 }}>
        //     <div className="onsite_barcode_">
        //       <img className="onsite_barcode_img_" src={UgcSettings.onsite_url_barcode} />
        //     </div>
        //     <div className={`onsite_text_ sGFfonte-${UgcSettings.onsite_txt_font_variation}`} style={{ fontSize: UgcSettings.onsite_txt_font_size, fontFamily: UgcSettings.onsite_txt_font }}>
        //       {UgcSettings.onsite_overlay_title}
        //     </div>
        //   </div> 
    }

}
export default memo(OnSiteUpload);