import React, { PureComponent } from "react";
import ReactHtmlParser from 'react-html-parser';  //{ processNodes, convertNodeToElement, htmlparser2 }
import Emojify from 'react-emojione';
class SlackEmoji extends PureComponent {

    render() {
        const { text } = this.props;
        return <Emojify>{ReactHtmlParser(text)}</Emojify> //<>{ReactHtmlParser(text)}</>///<Emoji text={text} />;
    }
}

export default SlackEmoji;