import axios from 'axios';
import { UGC_SUITE_WALL } from '../../utils'
import { UPDATE_INSTAGRAM_IMAGE_VIDEO, UPDATE_INSTAGRAM_IMAGE_VIDEO_U, UPDATE_COMMERCE_ONSITE_UPLOAD, UPDATE_COMMERCE_ONSITE_UPLOAD_U } from '../constants'
export const getShowLoadingScreen = (path, headers) => axios.get(path, headers);
export const getResults = (path, headers) => axios.get(path, headers);
export const getSocketData = (path, headers) => axios.get(path);
export const getDemoPost = (path, headers) => axios.get(path, { timeout: (1000 * 60 * 7) });

export const updateInstagramPostImage = (data) => {
    axios.post(`${UGC_SUITE_WALL() ? UPDATE_INSTAGRAM_IMAGE_VIDEO_U : UPDATE_INSTAGRAM_IMAGE_VIDEO}`, data)
        .then((response) => { })
        .catch((error) => {
            console.error(error);
        });
};

export const submitPostData = (data, parameters) => {
    return axios.post(`${UGC_SUITE_WALL() ? UPDATE_COMMERCE_ONSITE_UPLOAD_U : UPDATE_COMMERCE_ONSITE_UPLOAD}`, data, parameters)
}