import React, { memo, Component } from "react";
import { Helmet } from "react-helmet";
import './OnSiteUpload.css'
import { ONSITE_URL } from "../../studioPreview/actions/api";
import { UGC_SUITE_WALL } from '../../utils'
import QRCode from 'qrcode.react';

const OnsiteStripStyle = ({ onsite_txt_font_variation, onsite_txt_font, onsite_txt_css_font, onsite_txt_font_size }) => {
    return <Helmet><link href={`https://fonts.googleapis.com/css?family=${onsite_txt_css_font}:${onsite_txt_font_variation}`} rel="stylesheet" />
        <link
            href={`https://fonts.googleapis.com/css?family=${onsite_txt_font_size}:${onsite_txt_font_variation}`}
            rel="stylesheet" /></Helmet>
}


class OnSiteUpload extends Component {


    render() {

        const { UgcSettings, wallId } = this.props;

        return <>

            {
                UgcSettings && Object.keys(UgcSettings).length > 0 ? <OnsiteStripStyle {...UgcSettings} /> : null
            }
            <div className={`display-onsiteupload ${UgcSettings.onsite_overlay_poisition == "1" ? `onsiteupload_top_position` : UgcSettings.onsite_overlay_poisition == "2" ? `onsiteupload_bottom_position` : `onsiteupload_top_position`}`} style={{ backgroundColor: UgcSettings.onsite_btn_color ? UgcSettings.onsite_btn_color : UgcSettings.onsite_bg_color, color: UgcSettings.onsite_txt_color, height: `${UgcSettings.onsite_overlay_height ? UgcSettings.onsite_overlay_height : 120}px` }}>
                <div className="onsite_barcode_">
                    <QRCode style={{ height: '100%', width: '100%' }} value={`${ONSITE_URL}${UGC_SUITE_WALL() ? `0` : ``}${wallId}/onsite-upload`} renderAs="canvas" />,
                </div>
                <div className={`onsite_text_ sGFfonte-${UgcSettings.onsite_txt_font_variation}`} style={{ fontSize: UgcSettings.onsite_txt_font_size, fontFamily: UgcSettings.onsite_txt_font }}>
                    {UgcSettings.onsite_qr_code_btn_text}
                </div>
            </div>
        </>
    };
}
export default memo(OnSiteUpload);